import CommonHeader from '@/components/layouts/CommonHeader.vue';
export default {
	components:{
		CommonHeader
	},
	data() {
		return {
			userinfo: {},
			showBind: false,
			orderTypeLise: [{
					name: '待付款',
					icon: 'daifukuan.png',
					badge: 2
				},
				{
					name: '待使用',
					icon: 'daishiyong.png',
					badge: 2
				},
				{
					name: '未收货',
					icon: 'weishouhuo.png',
					badge: 6
				},
				{
					name: '待点评',
					icon: 'daidianping.png',
					badge: 9
				}
			],
			secondTypeLise: [{
					name: '优惠券',
					icon: 'youhuiquan.png',
					badge: 2
				},
				{
					name: '商品收藏',
					icon: 'shangpinshoucang.png',
					badge: 2
				},
				{
					name: '店铺收藏',
					icon: 'dianpushoucang.png',
					badge: 2
				},
				{
					name: '退款售后',
					icon: 'tuikuanshouhou.png',
					badge: 6
				},
			],
			severList: [
				[{
						name: '全部订单',
						icon: 'quanbudingdan.png'
					}, {
						name: '在线充值',
						icon: 'chongzhi.png'
					},
					{
						name: '更改绑定',
						icon: 'genggaibangding.png'
					},
					{
						name: '余额明细',
						icon: 'yue.png'
					},
					{
						name: '地址管理',
						icon: 'dizhi.png'
					}
				]
			],
		};
	},
	onShow() {
		//加载
		this.init();
	},
	methods: {
		init() {
			this.userinfo = uni.getStorageSync('userInfo')
		},
		toInnerType(index) {
			let url = ''
			if (index == 0) {
				url = "/pages/user/coupon"
			} else if (index == 1) {
				url = "/pages/user/product_collect"
			} else if (index == 2) {
				url = "/pages/user/store_collect"
			} else if (index == 3) {
				uni.makePhoneCall({
					phoneNumber: '18661139072'
				})
				return;
			}
			uni.navigateTo({
				url: url
			})
		},
		//用户点击订单类型
		toOrderType(index) {
			uni.navigateTo({
				url: "/pages/order/order_list?type=" + (index + 1)
			})
		},
		//用户点击列表项
		toServiceType(index) {
			let url = ''
			if (index == 0) {
				url = "/pages/order/order_list?type=0"
			} else if (index == 1) {
				url = "/pages/user/deposit"
			} else if (index == 2) {
				this.showBind = true
				return;
			} else if (index == 3) {
				url = "/pages/user/remain_money"
			} else if (index == 4) {
				url = "/pages/user/location_list"
			}
			uni.navigateTo({
				url: url
			})
		}
	}
}
